

@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup {
    background-color: v.$rosa;
    h3, .dia {
        text-align: center;
        color: v.$blanco;
        }
    .dia {
        font-size: 3rem;
        text-transform: uppercase;
    }
}

.escenarios-contenedor {
    @include m.contenedor; // mixin reutilizable
    @include m.tablet { // mixin que almacena otro mixin
        @include m.grid(2) // mixin con parametros
    }
}
.escenario {
    padding-top: 2rem;
    .nombre-escenario {
        font-size: 3rem;
        font-weight: 900;
        text-transform: uppercase;
        text-align: center;
    }
}
.calendario {
    @include m.resetear-lista;

    li {
        padding: 2.5rem;
        font-weight: 700;
    }
    span {
        font-weight: 400;
    }
}
.bg-amarillo {
    background-color: v.$amarillo;
    margin-bottom: 2rem;
    li:nth-child(odd) {
        background-color: lighten(v.$amarillo, 5%);
        }
        }
    .bg-verde {
        background-color: v.$verde;
        margin-bottom: 2rem;
    li:nth-child(odd) {
        background-color: lighten(v.$verde, 5%);
    }
}