@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-boletos {
    background-color: v.$verde;
    h3 {
        color: v.$blanco;
    }
    .boletos {
        @include m.contenedor;
        @include m.tablet {
            @include m.grid(2);
        }
    }
}

.pase {
    transition: transform .3s ease-in-out;
    &:hover {
       transform: scale(1.04); 
    }
    // &-nombre se usa como convención para no duplicar la palabra del blocke siempre, en este caso tenemos el bloque .pase y su elemento .pase-nombre, entonces &-nombre es lo mismo que pase-nombre
    &-nombre {
        font-size: 3.6rem;
        font-weight: 900;
        text-align: center;
        color: v.$blanco;
    }
    &-header, &-footer {
        padding: 0.5rem;

        p {
            text-align: center;
            color: v.$blanco;
            margin: 0;
        }
    }
    &-header {
        background-color: v.$rosa;
        p {
            text-transform: uppercase;
            font: 2.2rem;
            padding: 1rem 0;
        }
    }
    &-body {
        background-color: v.$blanco;
        ul {
            @include m.resetear-lista;
            li {
                padding: 2rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    &-footer {
        background-color: v.$blanco;
        padding: 3rem;

        p {
            font-size: 2.8rem;
            font-weight: 900;
            background-color: v.$morado;
            padding: 1rem 0;
            border-radius: 1rem;
            cursor: pointer;
            &:hover {
                background-color: darken(v.$morado, 20);
            }
        }
    }
}